body, html {
    margin: 0;
    padding: 0;
    height: 100%; 
    overflow: hidden;
}

.home-container {
    height: 100vh;
    width: 100vw; 
    margin-top: -60px;
    display: flex;
    margin-left: -60px;
    align-items: center;
    justify-content: center;
    background-size: cover;
    background-position: center;
    background-attachment: fixed; 
}

.home-content {
    max-width: 1000px;
    height: 500px;
    margin-top: -120px;
    padding: 20px;
    border-radius: 10px;
    text-align: center;
    color: #fff;
}

.home-content h1 {
    font-size: 8rem;
    margin-top: 80px;
    margin-bottom: 1rem;
    font-weight: bold;
}

.home-content p {
    font-size: 1.3rem;
    margin-bottom: 1.5rem;
    margin: 50px;
}

.explore-button {
    padding: 10px 20px;
    background-color: #f660c9;
    color: #fff;
    border: none;
    border-radius: 25px;
    cursor: pointer;
    font-size: 1.5rem;
    margin-left: -10px;
    transition: background-color 0.3s ease;
    width: 200px;
    height: 60px;
}

.explore-button:hover {
    background-color: #4a85e5;
}

@media (max-width: 425px) {
    .home-container {
        margin-left: -80px;
        margin-top: -60px;
        padding: 0;
        width: 180%;
        height: 100vh;
        display: flex;
        align-items: center;
        justify-content: center;
    }

    .home-content h1 {
        font-size: 2.6rem;
        margin-top: 150px;
        margin-left: -78px;
    }

    .home-content p {
        font-size: 14px;
        margin-top: 10px;
        left: -30px;
        margin-right: 20px;
        position: absolute;
    }

    .explore-button {
        font-size: 12px;
        width: 120px;
        height: 40px;
        margin-top: 90px;
        margin-left: -90px;
    }

    .menu-bar {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        background-color: rgba(0, 0, 0, 0.8);
        padding: 15px;
        z-index: 1000;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    .menu-icon {
        font-size: 2rem;
        color: #fff;
        cursor: pointer;
    }

    .menu-list {
        display: none;
        position: absolute;
        top: 50px;
        left: 0;
        background-color: rgba(0, 0, 0, 0.9);
        width: 100%;
        padding: 10px 0;
        text-align: center;
    }

    .menu-list.show {
        display: block;
    }

    .menu-list li {
        list-style: none;
        margin: 10px 0;
    }

    .menu-list li a {
        color: #fff;
        text-decoration: none;
        font-size: 1.5rem;
    }

    .x-icon::before {
        content: 'âœ–';
        font-size: 2rem;
        color: #fff;
    }

    .menu-open .x-icon {
        display: block;
    }

    .menu-open .menu-icon {
        display: none;
    }
}

@media (max-width: 375px) {
    .home-content h1 {
        font-size: 2.7rem;
        margin-top: 150px;
        margin-left: -40px;
    }

    .explore-button {
        font-size: 12px;
        width: 120px;
        height: 40px;
        margin-top: 90px;
        margin-left: -40px;
    }
}


@media (max-width: 320px) {
    .home-content h1 {
        font-size: 2.3rem;
        margin-top: 150px;
        margin-left: 8px;
    }

    .home-content p {
        font-size: 12px;
        margin-top: 10px;
        left: -30px;
        margin-right: 20px;
        position: absolute;
    }

    .explore-button {
        font-size: 12px;
        width: 120px;
        height: 40px;
        margin-top: 90px;
        margin-left: -5px;
    }
}

@media (min-width: 760px) and (max-width: 1024px) {
    .home-container {
        padding: 0;
        width: 120%;
        height: 100vh;
        display: flex;
        align-items: center;
        justify-content: center;
    }

    .home-content h1 {
        font-size: 4.5rem;
        margin-top: 110px;
    }

    .home-content p {
        font-size: 1.2rem;
    }

    .explore-button {
        font-size: 1.2rem;
        width: 180px;
        height: 50px;
    }
}