.about-container {
    height: 100vh;
    width: 100vw; 
    margin-top: -60px;
    display: flex;
    margin-left: -60px;
    align-items: center;
    justify-content: center;
    background-size: cover;
    background-position: center;
    background-attachment: fixed; 
}

.about-content {
    width: 780px;
    height: 400px;
    padding: 40px;
    margin-top: -90px;
    background-color: #fff;
    border-radius: 10px;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
    text-align: center;
    opacity: 0;
    transform: translateY(20px);
    animation: slide-up 1s forwards;
}

.about-content h1 {
    font-size: 4rem;
    margin-bottom: 2.5rem;
    margin-top: 20px;
    margin: 40px;
    font-weight: bold;
    color: #222;
}

.about-content p {
    font-size: 1.4rem;
    margin-bottom: 2.5rem;
    line-height: 1.8;
}

.team-section {
    display: flex;
    justify-content: space-around;
    margin-top: 2rem;
}

.team-member {
    text-align: center;
}

.team-member h3 {
    font-size: 1.5rem;
    margin-bottom: 0.5rem;
}

.team-member p {
    font-size: 1rem;
    color: #777;
}

@keyframes slide-up {
    to {
        opacity: 1;
        transform: translateY(0);
    }
}

/* Tablet Responsiveness */
@media (max-width: 1024px) {
    .about-content {
        width: 75%; 
        height: auto; 
        padding: 30px; 
    }

    .about-content h1 {
        font-size: 4rem; 
    }

    .about-content p {
        font-size: 1.25rem; 
    }

    .team-section {
        justify-content: space-between;
    }
}

@media (max-width: 768px) {
    .about-content {
        width: 75%; 
        height: 300px; 
        padding: 30px; 
    }

    .about-content h1 {
        font-size: 3rem; 
        margin-top: 10px;
    }

    .about-content p {
        font-size: 16px; 
        margin-top: 15px;
    }

    .team-section {
        justify-content: space-between;
        margin: 30px;
    }

    .team-member h3 {
        font-size: 17px;
        margin-bottom: 10px;
    }
    
    .team-member p {
        font-size: 13px;
        margin-left: 20px;
        color: #777;
    }
}


@media (max-width: 425px) {
    .about-content {
        width: 75%; 
        height: 300px; 
        padding: 30px; 
    }

    .about-content h1 {
        font-size: 2.8rem; 
        margin-top: 10px;
    }

    .about-content p {
        font-size: 15px; 
        margin-top: -20px;
    }

    .team-section {
        justify-content: space-between;
        margin: 0px;
    }

    .team-member h3 {
        font-size: 15px;
        margin-bottom: 10px;
    }
    
    .team-member p {
        font-size: 11px;
        margin-left: -10px;
        margin-top: 20px;
        color: #777;
    }
}



@media (max-width: 375px) {
    .about-content h1 {
        font-size: 2.5rem; 
        margin-top: 10px;
    }

    .about-content p {
        font-size: 13px; 
        margin-top: -20px;
    }

    .team-member h3 {
        font-size: 13px;
        margin-bottom: 10px;
    }
    
    .team-member p {
        font-size: 9px;
        margin-left: -10px;
        margin-top: 20px;
        color: #777;
    }
}



@media (max-width: 320px) {
    .about-content {
        width: 65%; 
        height: 250px; 
        padding: 30px; 
    }
    .about-content h1 {
        font-size: 1.5rem; 
        margin-top: 10px;
    }

    .about-content p {
        font-size: 14px; 
        margin-top: -20px;
    }

    .team-member h3 {
        font-size: 10px;
        margin-bottom: 10px;
        margin: -5px;
    }
    
    .team-member p {
        font-size: 8px;
        margin-left: -10px;
        margin-top: 20px;
        color: #777;
    }
}