body {
  font-family: 'Poppins', sans-serif;
  background: #1e1e3f;
  margin: 0;
  padding: 0;
}

.navbar {
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding: 15px;
  background: linear-gradient(145deg, #3e3e74, #282843);
  border-radius: 15px;
  box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.5);
}


.logo h2 {
  color: #61dafb;
  font-size: 2rem;
  font-family: 'Poppins', sans-serif;
  margin: 0;
  margin-left: -270px;
  letter-spacing: 2px;
}

.nav-links {
  list-style-type: none;
  margin: 0;
  padding: 0;
  display: flex;
  margin-right: -290px;
}

.nav-item {
  color: white;
  text-decoration: none;
  font-size: 1.5rem;
  padding: 10px 30px;
  background: linear-gradient(145deg, #ffffff33, #3e3e74);
  border-radius: 50px;
  margin: 8px;
  transition: all 0.3s ease;
}

.nav-item:hover {
  background: linear-gradient(145deg, #6060aa, #4a4a89);
  transform: scale(1.1);
}


.nav-link {
  color: white;
  text-decoration: none;
  font-size: 1.2rem;
  font-weight: 500;
  transition: color 0.3s ease, transform 0.3s ease;
}

.nav-link:hover {
  color: #61dafb;
  transform: scale(1.1);
}

/* Content styles */
.content {
  padding: 60px;
  text-align: center;
  min-height: 100vh;
  background-color: #f0f0f0;
}
.page-container {
  text-align: center;
  color: white;
  padding: 50px;
}

h1 {
  font-size: 3rem;
}

p {
  font-size: 1.25rem;
}
.fade-enter {
  opacity: 0;
}
.fade-enter-active {
  opacity: 1;
  transition: opacity 300ms ease-in;
}
.fade-exit {
  opacity: 1;
}
.fade-exit-active {
  opacity: 0;
  transition: opacity 300ms ease-out;
}

.menu-icon {
  display: none;
}


/* Tablet Styles */
@media (max-width: 1024px) {
  .navbar {
    padding: 15px;
    flex-direction: row;
  }
  .menu-icon,
  .x-icon {
    display: none;
  }

  .x-icon {
    display: none;
  }

  .logo h2 {
    font-size: 1.5rem;
    margin-left: -150px;
  }

  .nav-links {
    margin-right: -160px;
    display: flex;
    font-size: 1rem;
  }

  .nav-item {
    font-size: 1rem;
    padding: 8px 20px;
  }

  h1 {
    font-size: 2.5rem;
  }

  p {
    font-size: 1.2rem;
  }
}

@media (max-width: 768px) {
  .navbar {
    padding: 5px;
    flex-direction: row;
  }

  .logo h2 {
    font-size: 1.2rem;
    margin-left: -120px;
  }

  .nav-links {
    margin-right: -130px;
    font-size: 0.5rem;
  }

  .nav-link {
    font-size: 0.9rem;
  }

  .nav-item {
    font-size: 0.5rem;
    padding: 6px 10px;
  }

  h1 {
    font-size: 2.5rem;
  }

  p {
    font-size: 1.2rem;
  }
}

@media (max-width: 450px) {
  .navbar {
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 10px;
  }

  .nav-links {
      display: none; 
      flex-direction: column; 
      position: absolute;
      top: 60px; 
      right: 0;
      background: #3e3e74; 
      width: 100%; 
      padding: 10px 0;
      z-index: 1000;
      transition: max-height 0.5s ease, opacity 0.5s ease;
  }
  .logo h2 {
    font-size: 15px;
    margin-left: 10px;
    letter-spacing: 2px;
  }

  .nav-links.open {
      display: flex;
      right: 130px;
      opacity: 1; 
  }

  .nav-item {
      padding: 10px 20px;
      text-align: center;
      transition: background-color 0.3s ease, transform 0.3s ease;
  }

  .nav-item:hover {
    background-color: rgba(255, 255, 255, 0.2);
    transform: scale(1.05);
}

  .menu-icon {
      cursor: pointer;
      font-size: 1.5rem;
      display: flex;
      padding: 5px;
      margin-left: 250px;
      transition: transform 0.3s ease;
  }
  .menu-icon:hover {
    transform: scale(1.1);
  } 

  .nav-links.open {
      animation: slideDown 0.3s forwards; 
  }

  .nav-links.closed {
      animation: slideUp 0.3s forwards;
  }
}

@keyframes slideDown {
  0% {
      max-height: 0;
      opacity: 0;
  }
  100% {
      max-height: 300px; 
      opacity: 1;
  }
}

@keyframes slideUp {
  0% {
      max-height: 300px; 
      opacity: 1;
  }
  100% {
      max-height: 0;
      opacity: 0;
  }
}

