.contact-container {
    height: 100vh;
    width: 100vw; 
    margin-top: -60px;
    display: flex;
    margin-left: -60px;
    align-items: center;
    justify-content: center;
    background-size: cover;
    background-position: center;
    background-attachment: fixed; 
}

.contact-content {
    max-width: 600px;
    height: 470px;
    padding: 30px;
    margin-top: -70px;
    background-color: #fff;
    border-radius: 10px;
    box-shadow: 0 5px 20px rgba(0, 0, 0, 0.1);
    text-align: center;
    opacity: 0; 
    transform: translateY(20px); 
    animation: slide-up 1s forwards;
}

.contact-content h1 {
    font-size: 4rem;
    margin-bottom: 1.5rem;
    margin-top: 2px;
    color: #222;
}

.contact-content p {
    font-size: 1.1rem;
    margin-bottom: 2rem;
    margin: 20px;
    color: #666;
}

.contact-form {
    display: flex;
    flex-direction: column;
}

.contact-form input,
.contact-form textarea {
    padding: 10px;
    margin-bottom: 1rem;
    border: 1px solid #ccc;
    border-radius: 5px;
    font-size: 1rem;
}

.contact-form button {
    padding: 12px 20px;
    height: 50px;
    background-color: #f660c9;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 1rem;
    margin-bottom: 20px;
    transition: background-color 0.3s ease;
}

.contact-form button:hover {
    background-color: #4a85e5;
}

@keyframes slide-up {
    to {
        opacity: 1; 
        transform: translateY(0); 
    }
}

@media (max-width: 1440px){
    .contact-content {
        width: 60%; 
        height: 400px; 
        padding: 10px;
        margin-top: -50px
    }

    .contact-content h1 {
        font-size: 2.5rem; 
        margin-top: 20px;
    }

    .contact-content p {
        font-size: 14px;
    }

    .contact-form input,
    .contact-form textarea {
        font-size: 11px; 
    }

    .contact-form button {
        font-size: 15px;
        height: 40px;
    }
}
/* Responsiveness */
@media (max-width: 768px) {
    .contact-content {
        width: 60%; 
        height: 400px; 
        padding: 10px;
        margin-top: -50px
    }

    .contact-content h1 {
        font-size: 2.5rem; 
        margin-top: 20px;
    }

    .contact-content p {
        font-size: 14px;
    }

    .contact-form input,
    .contact-form textarea {
        font-size: 11px; 
    }

    .contact-form button {
        font-size: 15px;
        height: 40px;
    }
}


@media (max-width: 425px) {
    .contact-content {
        width: 75%; 
        height: 400px; 
        padding: 10px;
        margin-top: -50px
    }

    .contact-content h1 {
        font-size: 2.2rem; 
        margin-top: 20px;
    }

    .contact-content p {
        font-size: 12px;
    }

    .contact-form input,
    .contact-form textarea {
        font-size: 12px; 
    }

    .contact-form button {
        font-size: 13px;
    }
}


@media (max-width: 375px) {
    .contact-form input,
    .contact-form textarea {
        font-size: 10px; 
    }

}